           <InputGroup>\n                <InputGroupAddon>\n                    <i class=\"bi bi-key\"></i>\n                </InputGroupAddon>\n                <Password v-model=\"form.password\" :feedback=\"false\" class=\"w-100\" placeholder=\"Şifre\"/>\n            </InputGroup>\n          </div>\n\n          <div class=\"mb-7\">\n            <InputGroup>\n              <InputGroupAddon>\n                <i class=\"bi bi-key\"></i>\n              </InputGroupAddon>\n              <Password v-model=\"form.password_confirmation\" class=\"w-100\" :feedback=\"false\" autocomplete=\"new-password\" placeholder=\"Şifreniz tekrar\"/>\n            </InputGroup>\n          </div>\n          \n          <button class=\"btn btn-primary w-100\" type=\"submit\" :class=\"{ 'opacity-25': form.processing }\" :disabled=\"form.processing\">Başvuru Yap</button>\n\n          <FormErrors :form=\"form\" />\n\n          <div v-if=\"props.flash?.type == 'error'\" class=\"my-5 alert alert-danger\">\n            {{ props.flash.message }}\n          </div>\n          <div v-if=\"props.flash?.type == 'success'\" class=\"my-5 alert alert-success\">\n            {{ props.flash.message }}\n          </div>\n\n          <Divider class=\"my-10\"/>\n\n          <Link :href=\"route('login')\" class=\"btn btn-light w-100\">Üye Girişi</Link>\n\n        </form>\n      </div>\n\n      <div class=\"text-center mt-4\">\n\t\t\t\t<a href=\"/\" class=\"btn px-0\"><i class=\"bi bi-arrow-left\"></i> Siteye dön</a>\n\t\t\t</div>\n\n    </div>\n  </div>\n</template>\n\n<script>\nimport GuestLayout from \"../../Layouts/GuestLayout\";\nexport default {\n  layout: GuestLayout,\n};\n</script>\n\n<style>\nbody {\n  background-image: url(\"/dmn/media/auth/bg10.jpeg\");\n}\n\n[data-bs-theme=\"dark\"] body {\n  background-image: url(\"/dmn/media/auth/bg10-dark.jpeg\");\n}\n</style>\n\n<script setup>\nimport { useForm, Link } from \"@inertiajs/vue3\";\nimport InputText from 'primevue/inputtext';\nimport Password from 'primevue/password';\nimport FormErrors from '../../Components/FormErrors';\nimport InputGroup from 'primevue/inputgroup';\nimport InputGroupAddon from 'primevue/inputgroupaddon';\nimport Divider from 'primevue/divider';\n\nconst props = defineProps({\n  status: String,\n  flash:Object\n});\n\nconst form = useForm({\n  name: \"\",\n  email: \"\",\n  password: \"\",\n  password_confirmation: '',\n});\n\nconst submit = () => {\n  form.post(route(\"register\"), {\n    onFinish: () => form.reset(\"password\"),\n  });\n};\n</script>"],"sourceRoot":""}]);
